import { Component } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'earnville';

  public currentUrl: string = "";
  public actionClass: string = "btn-default bg-[#091117] w-full pl-6 py-7 justify-start font-normal rounded-none hover:bg-[#101d2788] button-menu-item text-[#F3BB4D] bg-[#101d2788]";
  public normalClass: string = "btn-default bg-[#091117] w-full pl-6 py-7 justify-start font-normal rounded-none hover:bg-[#101d2788] button-menu-item text-white hover:text-[#F3BB4D]";

  public menuClassHide : string = "flex-1 flex flex-col transition-all duration-300 background-layout dark:text-white md:pl-60";
  public menuClassShow : string = "flex-1 flex flex-col transition-all duration-300 background-layout dark:text-white md:pl-14"
  public isDesktop: boolean = true;

  public isMenuClick: boolean = false;

  constructor(private router: Router) {
    this.router.events.subscribe(event => {
      if(event instanceof NavigationEnd) {
        this.currentUrl = event.url;          
         console.log(this.currentUrl);

         //check mobile
         if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test((window.navigator.userAgent))){
          this.isDesktop = false;
        }else{
          this.isDesktop = true;
        }
      }
    })
  }

  /**Change Menu by click*/
  public clickMenu() {
    this.isMenuClick = ! this.isMenuClick;
  }
  
}
