import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AccountComponent } from './com/account/account.component';
import { BakedVilleComponent } from './com/baked-ville/baked-ville.component';
import { BetComponent } from './com/bet/bet.component';
import { CalculatorComponent } from './com/calculator/calculator.component';
import { CrashComponent } from './com/crash/crash.component';
import { DashboardComponent } from './com/dashboard/dashboard.component';
import { LiveJackpotComponent } from './com/live-jackpot/live-jackpot.component';
import { SwapComponent } from './com/swap/swap.component';

const routes: Routes = [
  { path: 'dashboard', component: DashboardComponent },
  { path: 'account', component: AccountComponent },
  { path: 'calculator', component: CalculatorComponent },
  { path: 'swap', component: SwapComponent }, 
  { path: 'live-jackpot', component: LiveJackpotComponent }, 
  { path: 'bet', component: BetComponent },
  { path: 'crash', component: CrashComponent }, 
  { path: 'baked-ville', component: BakedVilleComponent }, 
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
